.order-group {
    .content-block {
        margin: 0.5rem 0;
        padding: 0;
        color: #6d6d72;
    }
    .container-order {
        padding: 0.5rem;
        margin-bottom: 0.5rem;
        background-color: #ffffff;
    }
    .container-title {
        display: flex;
        justify-content: space-between;
        font-size: rem(16);
        border-bottom: 1px dotted #7b896d;
        padding: rem(10) 0;
    }
    .container-card-goods {
        font-size: rem(20);
        display: flex;
        padding: rem(16) 0;
        justify-content: space-between;
        align-items: center;
        ul {
            list-style: none;
            padding: 0;
            margin: 0;
            li {
                width: rem(80);
                height: rem(80);
                float: left;
                margin-right: rem(10);
                img {
                    height: 100%;
                    width: 100%;
                }
            }
        }
    }
    .pay_goods_btn {
        text-align: right;
        height: rem(56);
        padding-top: rem(16);
        border-top: 1px dotted #7b896d;
    }
    .pay_goods_btn .pay-btn {
        width: rem(108);
        font-size: rem(18);
        height: rem(34);
        line-height: rem(34);
        text-align: center;
        color: #ffffff;
        background-color: #dbbb69;
        outline: none;
        border: none;
        border-radius: rem(17);
    }
    .pay_goods_btn a {
        color: #ffffff;
    }
    /*无订单页面*/
    .not_paying {
        margin-top: 40%;
        justify-content: center;
        text-align: center;
        .not_paying_basket {
            width: 6rem;
            height: 6rem;
            border-radius: 50%;
            background-color: #ccc;
            text-align: center;
            margin: 0 auto;
            display: flex;
            align-items: center;
            justify-content: center;
            img {
                display: block;
                width: 4rem;
                height: 4rem;
            }
        }
    }
    .not_paying .not_pay_title {
        font-size: rem(22);
        color: #7b896d;
        letter-spacing: .15rem;
        margin: rem(20) 0 rem(30) 0;
    }
    .not_paying .go_shop {
        width: rem(130);
        font-size: rem(24);
        height: rem(44);
        line-height: rem(44);
        text-align: center;
        color: #dbba68;
        outline: none;
        border: 1px solid #dbba68;
        border-radius: rem(22);
        background-color: #f4f4f4;
    }
    .not_paying .go_shop a {
        color: #dbba68;
    }
}